.list {
  color: white;
  width: 300px;
  background-color: #bf07ac;
  height: 50px;
  margin: 20px auto;
  border-radius: 5px;
}
.list p {
  padding: 10px;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
}

.list p input {
  background-color: transparent;
  border: none;
  color: white;
  font-size: 18px;
}
.list p input:focus {
  outline: none;
}
i {
  cursor: pointer;
}
