body {
  background-color: #9266ff;
}
.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 5px;
}

#todo-form input {
  background-color: rgb(27, 122, 137);
  border: none;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 8px;
  font-size: 18px;
  color: white;
}

#todo-form input::placeholder {
  color: antiquewhite;
}

#todo-form input:focus,
#todo-form button:focus {
  outline: none;
}

#todo-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: none;
  background-color: rgb(202, 156, 24);
  color: antiquewhite;
  cursor: pointer;
  font-weight: bolder;
}
